@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .moreOptionsBtn {
        @apply w-full text-left px-4 py-2 hover:bg-[#F0F1F3];
    }
}
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;400;500;600&display=swap');

.responsive-site-builder #wpcontent {
    background-color: #fff;
    padding-left: 0;
    margin: 0;
}
.responsive-site-builder #responsive-sb-app-root {
    font-family: 'Libre Franklin', sans-serif;
}
.responsive_page_responsive-site-builder #wpcontent #wpbody-content {
    padding-bottom: 0;
}
.responsive_page_responsive-site-builder #wpcontent #wpbody-content .responsive-site-builder-sidebar {
    height: calc(100vh - 96px);
}
.responsive-site-builder #wpfooter {
    display: none;
}
/* Styles for Display User Conditions Modal */
.responsive-sb-admin-display-rules-modal .components-modal__content {
    padding: 0;
    margin-top: 67px;
}
.responsive-sb-admin-display-rules-modal .components-modal__header {
    padding: 24px;
    height: 68px;
    margin: 0;
    border-bottom: 1px solid #CED5DE;
}
.responsive-sb-admin-display-rules-modal .components-modal__header-heading {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0;
    color: #333;
}
.responsive-sb-meta-select .responsive__control {
    margin-top: 8px;
    cursor: pointer;
    border-color: #8c8f94;
}
.responsive-sb-meta-select .responsive__input-container .responsive__input {
    box-shadow: none;
}
.responsive__indicators .responsive__indicator {
    color: #8c8f94;
}
.responsive__indicators .responsive__indicator-separator {
    background-color: #8c8f94;
}
.responsive-sb-admin-modal-table .responsive-sb-admin-select {
    font-size: inherit;
    font-family: inherit;
    max-width: 100%;
    width: 100%;
}
.components-modal__content.has-scrolled-content:not(.hide-header) .components-modal__header {
    border-bottom-color: #007cba;
}
.responsive-site-builder #adminmenuback, .responsive-site-builder #adminmenumain, .responsive-site-builder #adminmenuwrap, .responsive-site-builder #wpadminbar, .responsive-site-builder #wpfooter {
    display: none;
}
.responsive-sb-admin-display-rules-modal button.components-button.is-compact.has-icon {
    min-width: 24px;
    width: 24px;
    height: 24px;
}